@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Finger+Paint&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  max-width: 100%;
  margin: 0 auto;
}

.App header {
  background: linear-gradient(-45deg, #d16848, #d8115d, #0cb1ec, #16723f);
  background-size: 400% 400%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
  font-family: "Finger Paint", cursive;
  font-size: 12px;
  height: 10vh;
  min-height: 50px;
  color: white;
  position: fixed;
  width: 100%;
  max-width: 100%;
  top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}

.App section {

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  
  min-height: 100vh;
  background-color: rgb(50, 46, 69);
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}


  .ChatRoom {
      height: 10vh;
      position: fixed;
      bottom: 0;
      background-color: rgb(24, 23, 23);
      width: 100%;
      max-width: 100%;
      display: -webkit-flex;
      display: flex;
      font-size: 1.2rem;
  }

  main {
      padding: 10px;
      height: 80vh;
      overflow-y: scroll;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction:  column-reverse;
              flex-direction:  column-reverse;
  }

  main::-webkit-scrollbar {
      width: 0.3rem;
  }

  main::-webkit-scrollbar-track {
      background: #1e1e24;
  }

  main::-webkit-scrollbar-thumb {
      background: #6649b8;
  }

  .ChatRoom button {
      border: none;
      border-radius: 0px 10px 10px 0px;
      color: white;
      height: 100%;
      text-align: center;
      text-decoration: none;
      display: inline-block;
      cursor: pointer;
      font-size: 1.8rem;
      background-color: rgb(66, 197, 92);
      width: 20%;
      margin-right: 0%;
  }

  .ChatRoom button:disabled {
      opacity: 0.4;
      cursor: not-allowed;
  }

  .ChatRoom button:disabled:hover {
      background-color: rgb(54, 160, 75);
      transition: background-color 0.2s;
  }

  .ChatRoom button:hover {
      background-color: rgb(5, 201, 5);
      transition: background-color 0.3s;

  }

  .ChatRoom textarea {
      line-height: 1.5;
      border-radius: 10px 0px 0px 10px;
      width: 90%;
      font-size: 1.5rem;
      background-color: #5b4b5b;

      /* background: rgb(86, 85, 85); */
      color: white;
      outline: none;
      border: none;
      padding: 0 10px;

      overflow-y: scroll;
      
      resize:none;


  }
  .ChatRoom textarea::-webkit-scrollbar {
    width: 0rem;
}

.ChatRoom textarea::-webkit-scrollbar-track {
    background: #1e1e24;
}

.ChatRoom textarea::-webkit-scrollbar-thumb {
    background: #6649b8;
}
.message {
    display: -webkit-flex;
    display: flex;
}

.message p {
    max-width: 500px;
    margin-top: 7px;
    margin-bottom: 12px;
    line-height: 24px;
    padding: 0px 5px;
    border-radius: 25px;
    position: relative;
    color: white;
    text-align: center;
}

.sent {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}

.sent p {
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}

.received p {
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    /* color: black; */
}

img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 4px 7px;
}

.UI {

    margin: 0px auto;
}

.UI h3 {
    padding-top: 25px;
    font-size: 35px;
    font-weight:bold; 
}


.form {
    font-family: "Finger Paint", cursive;
    font-size: 24px;
    background-color: white;
    width: 500px;
    height: 500px;
    min-height: 300px;
    box-shadow: 0px 0px 29px 2px rgb(0, 0, 0, 0.5);
    border-radius: 10px 10px 10px 10px;
}


.status {
    display: grid;
    grid-template-columns: 20% 30% 30% 20%;
    height: 10%;
    min-height: 30px;
    margin-bottom: 15px;
}

.status div {

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;

}

.status div:hover {
    background-color: rgb(0, 0, 0, 0.05);
    transition: background-color 0.2s;
}


.form input {
    font-family: "Finger Paint", cursive;
    border-radius: 25px 25px 25px 25px;
    width: 50%;
    height: 25px;
    font-size: 17px;
    outline: none;
    border-width:4px;
}

button {
    font-family: "Finger Paint", cursive;
    font-size: 24px;
    background-color: rgb(39, 39, 39);
    color: white;
    border-radius: 30px 30px 30px 30px;
    height: 50px;
    width: 150px;
    margin-right: 20px;
    cursor: pointer;
    outline: none;
}

button:hover {
    background-color: rgb(68, 68, 68);
    transition: background-color 0.2s;

}

img {
    width: 45px;
    height: 45px;

}

.floatline {
    border-bottom: 2px solid;
    width: 150px;
    height: 47px;
    position: absolute;
    cursor: none;
}
